@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Tulpen+One&display=swap);
.App{text-align:center}*{margin:0;padding:0;list-style:none;vertical-align:baseline}div{position:relative;z-index:2}body{transition:all linear 2.5s;background-color:#242320;background-size:cover;color:#333;font-family:'Montserrat', sans-serif;font-weight:380;display:flex;justify-content:center;align-items:center;height:100vh;width:100vw}.footer{width:380px;text-align:center;display:block;margin:10px auto 30px auto;font-size:0.9em;color:#fff}.footer a{font-weight:500;text-decoration:none;color:#fff}#quote-box{border-radius:3px;float:4em;position:relative;margin-left:900px;margin-bottom:350px;width:300px;padding:30px 40px;display:table;background-color:#242320}#quote-box .quote-text{text-align:center;width:350px;height:auto;clear:both;font-weight:500;font-size:1.3em}#quote-box .quote-text i{font-size:1.0em;margin-right:0.4em}#quote-box .quote-author{width:350px;height:auto;clear:both;padding-top:20px;font-family:'Tulpen One', cursive;font-size:1.6em;text-align:right}#quote-box .buttons{width:350px;margin:auto;display:block}#quote-box .buttons .button{height:38px;border:none;border-radius:3px;color:#242320;background-color:#333;outline:none;font-size:0.85em;padding:8px 18px 6px 18px;margin-top:30px;opacity:1;cursor:pointer}#quote-box .buttons .button:hover{opacity:0.9;color:#fff}#quote-box .buttons .button#tweet-quote{float:left;padding:0px;padding-top:8px;text-align:center;font-size:1.2em;margin-right:5px;height:30px;width:40px}#quote-box .buttons .button#new-quote{float:right}

